
#slider {
    overflow: hidden;
}
.fig_ure{
    position: relative;
    margin: 0%;
    width: 500%;
    left: 0;
    animation: 20s slider infinite;
    }
    .imgs_div{
        float: left;
        width: 20%;
        height: 350px;
    }

    @keyframes slider{
        0%{left: 0;} 
        20%{left: 0;}
        25%{left: -100%;}
        45%{left:-100%;}
        50%{left:-200%;}
        70%{left:-200%;}
        75%{left: -300%;}
        95%{left: -300;}
       100%{left:-300%;}
    }

    .hynudai{
       max-width: 1100px;
        width: 90%;
        float: left;
        margin-left: 50%;
        transform: translateX(-50%);
    }
